"use server";

import fs from "fs";
import path from "path";
import { v4 as uuidv4 } from "uuid";
import { getAuthInfo } from "./admin-auth";
import { revalidatePath, unstable_noStore as noStore } from "next/cache";

// Resolve DB path dynamically to avoid any process.cwd issues
const getDbPath = () => path.join(process.cwd(), "data", "payments.json");

export interface Payment {
    id: string;
    bookingId: string;
    studioId: string;
    studioName: string;
    userName: string;
    userEmail: string;
    userPhone?: string;
    amount: number;
    status: "paid" | "pending" | "failed";
    paymentMethod: "stripe" | "credit" | "other" | "bank_transfer";
    date: string;
    dueDate?: string;
    createdAt: string;
}

// Internal helper to read data with no-cache
async function _getAllPayments(): Promise<Payment[]> {
    noStore();
    const dbPath = getDbPath();
    try {
        if (!fs.existsSync(dbPath)) return [];
        const data = fs.readFileSync(dbPath, "utf-8");
        return JSON.parse(data);
    } catch (error) {
        console.error("[PaymentDB] Read error:", error);
        return [];
    }
}

export async function fetchPayments(): Promise<Payment[]> {
    const auth = await getAuthInfo();
    const payments = await _getAllPayments();

    if (auth.isAdmin) return payments;
    if (auth.studioId) return payments.filter(p => p.studioId === auth.studioId);
    return [];
}

export async function sendReminderEmail(paymentId: string, email: string) {
    console.log(`[Email] Reminder to ${email} for ${paymentId}`);
    await new Promise(r => setTimeout(r, 500));
    return { success: true };
}

export async function createPayment(data: any) {
    const payments = await _getAllPayments();
    const newPayment: Payment = {
        id: uuidv4(),
        ...data,
        status: "pending",
        date: new Date().toISOString().split("T")[0],
        dueDate: new Date(Date.now() + 3 * 24 * 60 * 60 * 1000).toISOString().split("T")[0],
        createdAt: new Date().toISOString(),
    };
    payments.push(newPayment);
    fs.writeFileSync(getDbPath(), JSON.stringify(payments, null, 2), "utf-8");
    revalidatePath("/admin/payments");
    return { success: true, paymentId: newPayment.id };
}

export async function updatePaymentStatus(paymentId: string, status: "paid" | "pending" | "failed") {
    noStore();
    console.log(`[ServerAction] Reconciling payment: ${paymentId} -> ${status}`);

    try {
        const dbPath = getDbPath();
        const data = fs.readFileSync(dbPath, "utf-8");
        const payments: Payment[] = JSON.parse(data);

        const index = payments.findIndex(p => p.id === paymentId);
        if (index === -1) {
            return { success: false, message: "対象データが見つかりませんでした。" };
        }

        payments[index].status = status;

        // Write synchronously to ensure it's done before returning
        fs.writeFileSync(dbPath, JSON.stringify(payments, null, 2), "utf-8");

        // Force Next.js to purge any cached versions of pages using this data
        revalidatePath("/admin/payments");
        revalidatePath("/admin");
        revalidatePath("/", "layout"); // Global revalidation for safety

        return { success: true };
    } catch (error: any) {
        console.error("[ServerAction] Reconcile error:", error);
        return { success: false, message: error.message };
    }
}

export async function handleStripeWebhook(payload: any) {
    console.log("Stripe Hook:", payload.type);
}
